html, body, #root {
  height: 100%;
  width: 100%;
  overflow-x: hidden;
}

.App {
  display: grid;
  gap: 5px;
  grid-template-columns: repeat(59, 10px);
  grid-template-rows: repeat(11, 60px);
  max-width: 60%;
  margin-left: auto;
  margin-right: auto;
  margin-top: 25px;
}

.box {
  text-align: center;
  color: white;
  font-weight: bolder;
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-width: 0;
}

.box-0{
  grid-column-start: 1;
  grid-column-end: 30;
  grid-row-start: 1;
  grid-row-end: 7;
}

.box-1{
  grid-column-start: 30;
  grid-column-end: 60;
  grid-row-start: 1;
  grid-row-end: 4;
}

.box-2{
  grid-column-start: 30;
  grid-column-end: 45;
  grid-row-start: 4;
  grid-row-end: 7;
}

.box-3{
  grid-column-start: 45;
  grid-column-end: 60;
  grid-row-start: 4;
  grid-row-end: 7;
}

.box-4{
  grid-column-start: 1;
  grid-column-end: 20;
  grid-row-start: 7;
  grid-row-end: 9;
}

.box-5{
  grid-column-start: 1;
  grid-column-end: 20;
  grid-row-start: 9;
  grid-row-end: 12;
}

.box-6{
  grid-column-start: 20;
  grid-column-end: 44;
  grid-row-start: 7;
  grid-row-end: 12;
}

.box-7{
  grid-column-start: 44;
  grid-column-end: 60;
  grid-row-start: 7;
  grid-row-end: 10;
}

.box-8{
  grid-column-start: 44;
  grid-column-end: 60;
  grid-row-start: 10;
  grid-row-end: 12;
}

@media (min-width:1001px) and (max-width:1150px) {

  .App {
    gap: 4px;
    grid-template-columns: repeat(59, 8px);
    grid-template-rows: repeat(11, 50px);
  }

}

@media (min-width:601px) and (max-width:1000px) {
  #root {
    padding: 0;
  }

  .App {
    gap: 2px;
    grid-template-columns: repeat(59, 5px);
    grid-template-rows: repeat(11, 30px);
  }
  
}

@media (max-width:600px) {

  #root {
    padding: 25px 0;
  }
  
  .App {
    grid-template-columns: repeat(6, 1fr);
    max-width: 80%;
  }
   
  .box-0{
    grid-column-start: 1;
    grid-column-end: 7;
    grid-row-start: 1;
    grid-row-end: 2;
  }
  
  .box-1{
    grid-column-start: 1;
    grid-column-end: 7;
    grid-row-start: 3;
    grid-row-end: 4;
  }
  
  .box-2{
    grid-column-start: 1;
    grid-column-end: 4;
    grid-row-start: 2;
    grid-row-end: 3;
  }
  
  .box-3{
    grid-column-start: 4;
    grid-column-end: 7;
    grid-row-start: 2;
    grid-row-end: 3;
  }
  
  .box-4{
    grid-column-start: 1;
    grid-column-end: 4;
    grid-row-start: 5;
    grid-row-end: 6;
  }
  
  .box-5{
    grid-column-start: 1;
    grid-column-end: 4;
    grid-row-start: 6;
    grid-row-end: 7;
  }
  
  .box-6{
    grid-column-start: 1;
    grid-column-end: 7;
    grid-row-start: 4;
    grid-row-end: 5;
  }
  
  .box-7{
    grid-column-start: 4;
    grid-column-end: 7;
    grid-row-start: 5;
    grid-row-end: 6;
  }
  
  .box-8{
    grid-column-start: 4;
    grid-column-end: 7;
    grid-row-start: 6;
    grid-row-end: 7;
  }
  
}